import React, {useRef, useEffect} from 'react';

import {gsap} from 'gsap';
import classes from './Home.module.css';

const Home = ()=>{

    const titleRef = useRef(null);
    const subtitleRef = useRef(null);
    const btnRef = useRef(null);

    useEffect(()=>{
        const tl = gsap.timeline();
        tl
            .from( titleRef.current, { 
                delay: 0.2,
                duration: 0.5,
                opacity: 0,
                y: '-100'
            } )
            .from( subtitleRef.current, {
                delay: 0.2,
                duration: 0.5,
                opacity: 0,
                y: '-100'
            } )
            .from( btnRef.current, {
                delay: 0.2,
                duration: 0.5,
                opacity: 0,
                y: '-100'
            } )

    }, [])


    return(
        <div className={classes.hero}>
            <h1 ref={titleRef} className={classes['hero-title']}>Your technology partner for innovative and impactful digital solutions.</h1>
            <p ref={subtitleRef} className={classes['hero-subtitle']}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Neque impedit ipsum illo, repellendus recusandae quasi laborum</p>
            <button ref={btnRef} className={classes['hero-btn']}>Get started</button>
        </div>
    )
}

export default Home;