import React, {useEffect, useRef} from 'react';

import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';

import classes from './Teams.module.css';

import Suhail from '../imgs/suhail.png';
import Wahid from '../imgs/wahid.png';
import Sahal from '../imgs/sahal.png';

gsap.registerPlugin( ScrollTrigger );

const Teams = ()=> {

    const teams = useRef([]);
    teams.current = [];

    const addToTeam = team => {
        if( team && !teams.current.includes(team) ){
            teams.current.push(team);
        }
    }

    useEffect( () => {

        teams.current.forEach( team => {
            gsap.from( team, {
                duration: 0.5,
                opacity: 0,
                y: 200,
                scrollTrigger: {
                    trigger: team,
                    start: "top bottom"
                }
            } )
        } )

    }, [] )

    return(
       
            <div className={classes.teams+' md2-sm1'}>
                {/* Team starts */}
                <div ref={addToTeam} className={classes.team}>
                    <div className={classes['feature-img']}>
                        <img src={Suhail} alt="suhail_habib" />
                    </div>
                    <div className={classes['team-body']}>
                        <h2 className={classes.title}>Suhail Habib</h2>
                        <h4 className={classes.designation}>Full Stack Dev</h4>
                        <p className={classes['short-bio']}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ducimus accusantium, temporibus distinctio commodi harum</p>
                        <ul className={classes['social-links']}>
                            <li className={classes['social-link']}>
                                <a href="www.linkedin.com" className={classes['social-icon']}>l<i class="fa fa-linkedin" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.facebook.com" className={classes['social-icon']}>f<i class="fa fa-facebook-square" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.twitter.com" className={classes['social-icon']}>t<i class="fa fa-twitter" aria-hidden="true"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
                {/* Team ends */}
                {/* Team starts */}
                <div ref={addToTeam} className={classes.team}>
                    <div className={classes['feature-img']}>
                        <img src={Wahid} alt="suhail_habib" />
                    </div>
                    <div className={classes['team-body']}>
                        <h2 className={classes.title}>Suhail Habib</h2>
                        <h4 className={classes.designation}>Full Stack Dev</h4>
                        <p className={classes['short-bio']}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ducimus accusantium, temporibus distinctio commodi harum</p>
                        <ul className={classes['social-links']}>
                            <li className={classes['social-link']}>
                                <a href="www.linkedin.com" className={classes['social-icon']}>l<i class="fa fa-linkedin" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.facebook.com" className={classes['social-icon']}>f<i class="fa fa-facebook-square" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.twitter.com" className={classes['social-icon']}>t<i class="fa fa-twitter" aria-hidden="true"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
                {/* Team ends */}
                {/* Team starts */}
                <div ref={addToTeam} className={classes.team}>
                    <div className={classes['feature-img']}>
                        <img src={Sahal} alt="suhail_habib" />
                    </div>
                    <div className={classes['team-body']}>
                        <h2 className={classes.title}>Suhail Habib</h2>
                        <h4 className={classes.designation}>Full Stack Dev</h4>
                        <p className={classes['short-bio']}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ducimus accusantium, temporibus distinctio commodi harum</p>
                        <ul className={classes['social-links']}>
                            <li className={classes['social-link']}>
                                <a href="www.linkedin.com" className={classes['social-icon']}>l<i class="fa fa-linkedin" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.facebook.com" className={classes['social-icon']}>f<i class="fa fa-facebook-square" aria-hidden="true"></i></a>
                            </li>
                            <li className={classes['social-link']}>
                                <a href="www.twitter.com" className={classes['social-icon']}>t<i class="fa fa-twitter" aria-hidden="true"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
                {/* Team ends */}
            </div>
    )
}

export default Teams;