import React from 'react';

import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import Slider from "react-slick";

import classes from './Projects.module.css';

import Project1 from '../imgs/projects/project1.jpg';

const Projects = ()=>{

    const setting = {
        dots: true,
        className: "center",
        centerMode: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        responsive:[
            {
                breakpoint: 760,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    centerMode: false
                }
            }
        ]
    }

    return(
        <Slider {...setting} className={classes.projects}>
            <div className={classes.project}>
                <div className={classes.feature}>
                    <img src={Project1} alt="" />
                </div>
                <div className={classes['project-body']}>
                    <h3 className={classes['project-title']}>Project</h3>
                    <h2 className={classes.title}>Parfinos</h2>
                    <p className={classes.description}>Branding, website</p>
                </div>
            </div>
            <div className={classes.project}>
                <div className={classes.feature}>
                    <img src={Project1} alt="" />
                </div>
                <div className={classes['project-body']}>
                    <h3 className={classes['project-title']}>Project</h3>
                    <h2 className={classes.title}>Parfinos2</h2>
                    <p className={classes.description}>Branding, website</p>
                </div>
            </div>
            <div className={classes.project}>
                <div className={classes.feature}>
                    <img src={Project1} alt="" />
                </div>
                <div className={classes['project-body']}>
                    <h3 className={classes['project-title']}>Project</h3>
                    <h2 className={classes.title}>Parfinos3</h2>
                    <p className={classes.description}>Branding, website</p>
                </div>
            </div>
            <div className={classes.project}>
                <div className={classes.feature}>
                    <img src={Project1} alt="" />
                </div>
                <div className={classes['project-body']}>
                    <h3 className={classes['project-title']}>Project</h3>
                    <h2 className={classes.title}>Parfinos3</h2>
                    <p className={classes.description}>Branding, website</p>
                </div>
            </div>
        </Slider>
    )
}

export default Projects;