import React, {useRef, useEffect} from 'react';

import {gsap} from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";

import classes from './AboutUs.module.css';

import Tool1 from '../imgs/tools/tool1.png';
import Tool2 from '../imgs/tools/tool2.png';
import Tool3 from '../imgs/tools/tool3.png';
import Tool4 from '../imgs/tools/tool4.png';
import Tool5 from '../imgs/tools/tool5.png';
import Tool6 from '../imgs/tools/tool6.png';
import Tool7 from '../imgs/tools/tool7.png';
import Tool8 from '../imgs/tools/tool8.png';

import AboutUsImg from '../imgs/about-us.jpg';
gsap.registerPlugin(ScrollTrigger);

const AboutUs = ()=>{

    const aboutSection = useRef(null);
    const aboutLeft = useRef(null);
    const aboutRight = useRef(null);
    const parent = useRef();
    const item1 = useRef();
    const item2 = useRef();
    const item3 = useRef();
    const item4 = useRef();
    const item5 = useRef();
    const item6 = useRef();
    const item7 = useRef();
    const item8 = useRef();

    useEffect( ()=>{

        const tl = gsap.timeline();

        tl
        .from( parent.current, {
            opacity: 0,
            scrollTrigger: {
                trigger: parent.current,
                start: "top bottom"
            }
        } )
        .from(item1.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item2.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item3.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item4.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item5.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item6.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item7.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })
        .from(item8.current, {
            delay: 0.1,
            opacity: 0,
            y: 100,
            duration: 0.2,
        })


        gsap.from( aboutLeft.current, {
            duration: 1,
            y: 200,
            opacity: 0,
            scrollTrigger: {
                trigger: aboutSection.current,
                start: 'top bottom'
            }
        } )

        gsap.from( aboutRight.current, {
            duration: 0.5,
            y: 200,
            opacity: 0,
            scrollTrigger: {
                trigger: aboutSection.current,
                start: 'top bottom'
            }
        } )

    }, [] )

    return(
        <div ref={aboutSection} className="grid-2">
            <div ref={aboutLeft} className="left">
                <h2 className={classes.title}>About Cubix</h2>
                <p className={classes.description}>We are NRGthemes and we build awesome WordPress Themes. We provide professionally designed themes to help your WordPress powered site standout in the crowd.</p>
                <h4 className={classes['sub-title']}>Tools</h4>
                <ul ref={parent} className={classes.tools}>
                    <li ref={item1} className={classes.tool}><img src={Tool1} alt="tool1" /></li>
                    <li ref={item2} className={classes.tool}><img src={Tool2} alt="tool2" /></li>
                    <li ref={item3} className={classes.tool}><img src={Tool3} alt="tool3" /></li>
                    <li ref={item4} className={classes.tool}><img src={Tool4} alt="tool4" /></li>
                    <li ref={item5} className={classes.tool}><img src={Tool5} alt="tool5" /></li>
                    <li ref={item6} className={classes.tool}><img src={Tool6} alt="tool6" /></li>
                    <li ref={item7} className={classes.tool}><img src={Tool7} alt="tool7" /></li>
                    <li ref={item8} className={classes.tool}><img src={Tool8} alt="tool8" /></li>
                </ul>
            </div>
            <div ref={aboutRight} className={classes.right}>
                <img src={AboutUsImg} alt="about-us" />
            </div>
        </div>
    )
}

export default AboutUs;