import React, {useRef, useEffect} from 'react';

import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';

import classes from './OurServices.module.css';

import ServiceIcon1 from '../imgs/icons/serv_icon_1.png';
import ServiceIcon2 from '../imgs/icons/serv_icon_2.png';
import ServiceIcon3 from '../imgs/icons/serv_icon_3.png';

gsap.registerPlugin( ScrollTrigger );

const OurServices = ()=>{

    const serviceImgs = useRef([]);
    serviceImgs.current = [];

    const serviceTitles = useRef([]);
    serviceTitles.current = [];

    const serviceDescriptions = useRef([]);
    serviceDescriptions.current = [];

    const addToServiceImgs = img => {
        if( img && !serviceImgs.current.includes(img) ){
            serviceImgs.current.push( img );
        }
    }

    const addToServiceTitles = el => {
        if( el && !serviceTitles.current.includes(el) ){
            serviceTitles.current.push( el );
        }
    }

    const addToServiceDescription = el => {
        if( el && !serviceDescriptions.current.includes(el) ){
            serviceDescriptions.current.push( el );
        }
    }

    useEffect( ()=> {

        serviceTitles.current.forEach( (title) => {
            gsap.from( title, {
                opacity: 0,
                duration: 0.5,
                y: 100,
                scrollTrigger: {
                    trigger: title,
                    start: "top bottom"
                }
            } )
        } )

        serviceImgs.current.forEach( (title) => {
            gsap.from( title, {
                opacity: 0,
                duration: 0.5,
                y: 100,
                scrollTrigger: {
                    trigger: title,
                    start: "top bottom"
                }
            } )
        } )

    }, [] )

    return(
        <div className={classes.services+' md2-sm1'}>
            {/* Service starts */}
            <div className={classes.service}>
                <div className={classes.feature}>
                    <img ref={addToServiceImgs} src={ServiceIcon1} alt="UX/UI Design" />
                </div>
                <h1 ref={addToServiceTitles} className={classes.title}>UX/UI Design</h1>
                <p ref={addToServiceDescription} className={classes.description}>Expedita nam natus non dolorem repellendus accusantium similique fugiat earum</p>
            </div>
            {/* Service starts */}
            {/* Service starts */}
            <div className={classes.service}>
                <div className={classes.feature}>
                    <img ref={addToServiceImgs} src={ServiceIcon2} alt="Web Development" />
                </div>
                <h1 ref={addToServiceTitles} className={classes.title}>DEVELOPMENT</h1>
                <p ref={addToServiceDescription} className={classes.description}>Expedita nam natus non dolorem repellendus accusantium similique fugiat earum</p>
            </div>
            {/* Service starts */}
            {/* Service starts */}
            <div className={classes.service}>
                <div className={classes.feature}>
                    <img ref={addToServiceImgs} src={ServiceIcon3} alt="Support" />
                </div>
                <h1 ref={addToServiceTitles} className={classes.title}>SUPPORT</h1>
                <p ref={addToServiceDescription} className={classes.description}>Expedita nam natus non dolorem repellendus accusantium similique fugiat earum</p>
            </div>
            {/* Service starts */}
        </div>
    )
}

export default OurServices;