import React, { useEffect, useRef } from 'react';

import {gsap} from 'gsap';
import {ScrollTrigger} from 'gsap/ScrollTrigger';

import SectionTitle from '../components/layout/SectionTitle';

import classes from './ContactUs.module.css';

gsap.registerPlugin( ScrollTrigger );

const ContactUs = ()=>{

    const items = useRef([]);
    items.current = [];

    const addToItems = item => {
        if( item && !items.current.includes(item) ){
            items.current.push(item);
        }
    }

    useEffect( ()=>{

        items.current.forEach( item => {
            gsap.from( item, {
                duration: 0.5,
                opacity: 0,
                y: 200,
                scrollTrigger: {
                    trigger: item,
                    start: "top bottom"
                }
            } )
        } )

    }, [] )

    return(
        <div className="grid-2">
            <div className={classes['contact-details']}>
                <div>
                    <SectionTitle title="CONTACT US" subTitle="Consectetur adipisicing elit. Autem asperiores rerum alias." />
                </div>
                <ul className={classes.lists}>
                    <li className={classes.list}>
                        <h4 ref={addToItems} className={classes.title}>EMAIL</h4>
                        <p ref={addToItems} className={classes.description}>say_hello@clover.com</p>
                    </li>
                    <li className={classes.list}>
                        <h4 ref={addToItems} className={classes.title}>PHONE</h4>
                        <p ref={addToItems} className={classes.description}>+38 (555) 123 9876</p>
                    </li>
                    <li className={classes.list}>
                        <h4 ref={addToItems} className={classes.title}>ADDRESS</h4>
                        <p ref={addToItems} className={classes.description}>244 5th Avenue. Suite 1735<br></br>
                            New York, N.Y.<br></br>
                            USA, 10001</p>
                    </li>
                </ul>
            </div>
            <div className={classes['contact-form']}>
                <h2 className={classes['form-title']}>HAVE A QUESTIONS?</h2>
                <form action="">
                    <div ref={addToItems} className={classes['form-group']}>
                        <input type="text" className={classes.input} placeholder='NAME*' />
                    </div>
                    <div ref={addToItems} className={classes['form-group']}>
                        <input type="email" className={classes.input} placeholder='EMAIL*' />
                    </div>
                    <div ref={addToItems} className={classes['form-group']}>
                        <input type="email" className={classes.input} placeholder='SUBJECT*' />
                    </div>
                    <div ref={addToItems} className={classes['form-group']}>
                        <textarea name="" className={classes.input+" " +classes.textarea} id="" cols="30" rows="10" placeholder='MESSAGE*'></textarea>
                    </div>
                    <button ref={addToItems} className={classes['form-btn']}>SEND</button>
                </form>
            </div>
        </div>
    )
}

export default ContactUs;